//sass src/scss/intro.scss src/css/intro.css

.intro {
  img {
    width: 50vw;
    max-width: 700px;
    margin: auto;
  }
  #mainBtn {
    margin: 3rem auto;
    width: 50vw;
  }
}
@media (max-width: 800px) {
  .intro {
    img {
      width: 90vw;
    }
    #mainBtn {
      width: 90vw;
    }
  }
}
